.resourceWrapper {
    width: 100%;
    background-color: #fff;
    font-size: 14px;
    display: flex;
    border-right: 1px solid rgb(198, 195, 195);
    border-bottom: 1px solid rgb(198, 195, 195);
    box-sizing: border-box;
    align-items: center;
}

.resourceText {
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
